.toast {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 12px 16px;
  border-radius: 4px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  z-index: 9999;
  transition: all 0.3s ease;
  max-width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.toast-info {
  background-color: #2196F3;
  color: white;
}

.toast-success {
  background-color: #4CAF50;
  color: white;
}

.toast-warning {
  background-color: #FF9800;
  color: white;
}

.toast-error {
  background-color: #F44336;
  color: white;
}

.toast-content {
  flex: 1;
}

.toast-close {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  margin-left: 10px;
  padding: 0 5px;
}

.toast p {
  margin: 0;
  padding: 0;
  word-break: break-word;
}